<template>
    <section class="flex-1 flex flex-col mx-2">
        <div class="">
            <v-tabs v-model="tab" fixed-tabs bg-color="primary">
                <v-tab value="one">Imagens</v-tab>
                <v-tab value="two">Características</v-tab>
            </v-tabs>
        </div>
        <div class="flex flex-row justify-between text-[#ffffffbe] py-1 px-3 bg-[#704aa6] border-y-[1px]">
            <div class="text-start">
                <p>{{ localization }}</p>
                <p>{{ category }}</p>
            </div>
            <div class="text-end">
                <p>
                    <span class="font-semibold">{{ price }}</span> <span>{{ moeda }}</span>
                </p>
                <p>{{ purpose }}</p>
            </div>
        </div>
        <v-window v-model="tab">
            <v-window-item value="one" class="max-h-[calc(100vh-210px)]">
               
                <v-carousel elevation progress hide-delimiters class="">
                    <v-carousel-item v-for="(image, index) in photos" :key="index" :src="image"> </v-carousel-item>
                </v-carousel>
                
            </v-window-item>

            <v-window-item value="two" class="max-h-[calc(100vh-210px)] overflow-auto">
                <div class="description p-2">
                    <p class="text-[#795f9e] dark:text-[#8350a3] text-2xl font-bold">{{ title }}</p>
                    <h2 class="text-[#300769] dark:text-[#8356c2] font-bold">Características:</h2>
                    <div>
                        <div class="flex flex-col gap-1">
                            <p>{{ rentFrequency }}</p>
                            <p v-if="rooms > 0">
                                {{ rooms }}
                                <span v-if="rooms > 1">Quartos</span>
                                <span v-else-if="({ rooms } = 1)">Quarto</span>
                            </p>
                            <p v-if="baths > 0">
                                {{ baths }}
                                <span v-if="baths > 1">Casas de banho</span>
                                <span v-else-if="`${baths} = 1`">Casa de banho</span>
                            </p>
                            <p>{{ furnishingStatus }}</p>
                            <p v-if="amenities > 0">
                                Com: <span v-for="(amenities, index) in amenities" :key="index">{{ amenities }}, </span>...
                            </p>
                            <h2 class="text-[#300769] dark:text-[#8356c2] font-bold">Descrição:</h2>
                            <h3 class="indent-2 font-semibold text-base text-[#300769] dark:text-[#8356c2]">Interior</h3>
                            <ul class="indent-5" v-for="(item, index) in interior" :key="index">
                                <li>{{ item }}</li>
                            </ul>
                            <h3 class="indent-2 font-semibold text-base text-[#300769] dark:text-[#8356c2]">Exterior</h3>
                            <ul class="indent-5" v-for="(item, index) in exterior" :key="index">
                                <li>{{ item }}</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </v-window-item>
        </v-window>
    </section>
</template>

<script>
    export default {
        data() {
            return { tab: null };
        },
        props: {
            localization: String,
            category: String,
            price: String,
            moeda: String,
            purpose: String,
            title: String,
            rentFrequency: String,
            rooms: Number,
            baths: Number,
            furnishingStatus: String,
            amenities: Array,
            photos: Array,
            interior: Array,
            exterior: Array,
        },
    };
</script>

<style scoped>
    section {
        -webkit-box-shadow: 24px 12px 110px -39px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 24px 12px 110px -39px rgba(0, 0, 0, 0.75);
        box-shadow: 24px 12px 110px -39px rgba(0, 0, 0, 0.75);
    }
</style>
<style>
    .v-progress-linear__determinate {
        background: #000;
    }

    .v-carousel-item > div {
        margin: 10px;
    }
</style>
