import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import PropertiesView from "../views/PropertiesView.vue";
import PropertyView from "../views/PropertyView.vue";
import ShowProperty1 from "@/components/houses/ShowProperty1.vue";
import ShowProperty2 from "@/components/houses/ShowProperty2.vue";
const routes = [
    { path: "/", name: "home", component: HomeView },
    { path: "/propriedades", name: "propiedades", component: PropertiesView },
    {
        path: "/propriedade/detalhes",
        component: PropertyView,

        children: [
            {
                path: "1",
                name: "1",
                component: ShowProperty1,
            },
            {
                path: "2",
                name: "2",
                component: ShowProperty2,
            },
        ],
    },
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
});

export default router;
